.login__wrapper {
  overflow: hidden;
  font-family: Montserrat;
  height: 100vh;
}

.circle__login {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  background-color: #fff;
  border-radius: 10px;
}

.circle__login h1 {
  text-align: center;
  padding: 0 0 20px 0;
  border-bottom: 1px solid silver;
}

.circle__login form {
  padding: 0 40px;
  box-sizing: border-box;
}

form .txt__field {
  position: relative;
  border-bottom: 2px solid #adadad;
  margin: 30px 0px;
}

.txt__field input {
  width: 100%;
  padding: 0 5px;
  height: 40px;
  font-size: 16px;
  border: none;
  background: none;
  outline: none;
}

.txt__field label {
  position: absolute;
  top: 50%;
  left: 5px;
  color: #adadad;
  transform: translateY(-50%);
  font-size: 16px;
  pointer-events: none;
  transition: 0.5s;
}

.txt__field span::before {
  content: '';
  position: absolute;
  top: 40px;
  left: 0;
  width: 0;
  height: 2px;
  background: #2691d9;
  transition: 0.5s;
}

.txt__field input:focus ~ label,
.txt__field input:valid ~ label {
  top: -5px;
  color: #2691d9;
}

.txt__field input:focus ~ span::before,
.txt__field input:valid ~ span::before {
  width: 100%;
}

.pass {
  margin: -5px 0px 20px 5px;
  color: #a6a6a6;
  cursor: pointer;
}

.pass:hover {
  text-decoration: underline;
}

input[type='submit'] {
  width: 100%;
  height: 50px;
  border: 1px solid;
  background: #2691d9;
  border-radius: 25px;
  font-size: 18px;
  color: #e9f4fb;
  font-weight: 700;
  cursor: pointer;
  outline: none;
}

input[type='submit']:hover {
  border-color: #2691d9;
  transition: 0.5s;
}

#google {
  background: #050607;
  margin-bottom: 7px;
}

.signup_link {
  margin: 30px 0px;
  text-align: center;
  font-size: 16px;
  color: #666666;
}

.signup_link a {
  color: #2691d9;
  text-decoration: none;
}

.signup_link a:hover {
  text-decoration: underline;
}
